const automationCreationInitialState = {
    automationTarget: null,
    automationTrigger: null,
    automationAction: null,
};
function automationCreationReducer(state, action) {
    switch (action.type) {
        case "SET_TARGET":
            return Object.assign(Object.assign({}, state), { automationTarget: action.payload, automationTrigger: null, automationAction: null });
        case "SET_TRIGGER":
            return Object.assign(Object.assign({}, state), { automationTrigger: action.payload, automationAction: null });
        case "SET_ACTION":
            return Object.assign(Object.assign({}, state), { automationAction: action.payload });
        case "RESET":
            return automationCreationInitialState;
        default:
            return state;
    }
}
export { automationCreationInitialState, automationCreationReducer, };
